<template>
    <div class="main-login">
        <a-card :bordered="false" style="width: 300px;height: 250px;margin-top: -50px">
            <p>:</p>
            <a-input placeholder="" style="margin-bottom:5px " v-model="username"/>
            <p>:</p>
            <a-input placeholder="" type="password" style="margin-bottom:5px " v-model="password"/>
            <div style="display:flex; padding: 10px 0;justify-content: center;box-sizing: border-box">
                <a-button type="primary" @click="login"></a-button>
            </div>

        </a-card>
    </div>
</template>

<script>
    import Storage from '../plugins/Storage';

    export default {
        name: "Login",
        data(){
            return{
                username:'',
                password:''
            }
        },
        methods:{
            login(){
                let info={
                    username: this.username,
                    password: this.password
                }
                axios.get("/manage/login",{
                    params:info
                })
                    .then(function (data) {
                        if(data.status){
                            let storage=new Storage();
                            storage.setItem({
                                name:'token',
                                value:data.token
                            })
                            this.$router.push({name:'manage'});
                        }else {
                            this.$message.error('');
                        }

                    })
                    .catch(function (err) {
                        console.log(err)
                    })
            }
        }
    }
</script>

<style scoped lang="stylus">
    .main-login
        display flex;
        width 100%;
        height 100vh;
        justify-content center
        align-items center
        background-image: linear-gradient(to right , #7A88FF, #7AFFAF);

</style>